.acf-block-preview .social-share {
  pointer-events: none;
}

.social-share .footer_share {
  font-size: 1.25rem;
  font-weight: bold;
  background: #fefefe;
  padding: 30px 0;
}
.social-share .footer_share p {
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -moz-flex-direction: row;
  -webkit-flex-direction: row;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.social-share .footer_share p a {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-left: 20px;
}
.social-share .footer_share p a svg {
  width: 32px;
}